
body{
  overflow: hidden;
}
/*mobile first*/
@media only screen and (max-width: 768px) {

  #wrapper{
    height: 800px;
    background-image: url("/assets/images/bg.png");
    overflow: scroll;
  }

  #lacer-menu{
    height: auto;

    background-color: #ffffff;
    -webkit-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
    -moz-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
    box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  }

}

#lacer-head .title{
  font-size: 22px;
  color: #333333;
}


/*Desktops and larger*/
@media only screen and (min-width: 768px) {

  #wrapper{
    height: 800px;
    background-image: url("/assets/images/bg.png");
    overflow-y: scroll;
  }
  #lacer-menu{
    height: auto;
    margin: 50px;
    background-color: #ffffff;
    -webkit-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
    -moz-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
    box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  }




}

#lacer-head{
  width: 100%;
  height: 50px;
  padding-top: 30px;
  text-align: center;
  /*background: #ffffff;*/
  box-shadow: 0 2px 2px #cccccc;
  background-image: url("/assets/images/bg1.png");
  /*//border-bottom: #b94a48 solid 2px;*/
}

#lacer-login{
  width: 400px;
  height: auto;
  padding-bottom: 10px;
  background-color: #f5f5f5;
  margin: 100px auto;
  text-align: center;
  -webkit-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  -moz-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
}
#lacer-login .head{
  padding-top: 9px;;
  padding-bottom: 7px;
  font-size: 24px;
  color: #862165;
  font-family: "OpenSansBold","Helvetica Neue",Helvetica,Arial,sans-serif;
}
#lacer-login .sub-head{
  padding-bottom: 7px;
  font-size: 18.2px;
  color: #666;
}
#lacer-login > form {
  width: 100%;
  height: auo;
  margin: 0 auto;
  text-align: center;
}

#lacer-login > form > input {
  display: inline-block;
  width: 260px;
  height: 50px;
  margin: 5px auto;
  padding: 10px;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 0.8em;
  border: 1.5px solid #cccccc;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

#lacer-login > form > input:focus {
  border: thin solid #015b7e;
  outline: none;
}
.login-submit{
  width: 130px;
  height: 30px;
  background: #015b7e;
  border: none;
  outline: none;
  margin: 0 auto;
  padding-top: 12px;
  display: block;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 10px;
text-decoration: none;
  border-radius: 3px;
  cursor: pointer;
  font-weight: 700;
  font-size: 0.8em;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.login-submit:hover {
  background: #014965;
}
#lacer-login .line{
  width: 70%;;
  height: 2px;
  background-color: #cccccc;
  margin: 0 auto;

}
#lacer-login .down{
  font-size: 14px;
  color: #862165;
  margin-left: 10px;
  margin-right: 10px;

}

#lacer-content{
  height: 600px;
}
#lacer-menu .head{
  width: 100%;
  height: auto;
  padding-top: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #999999;
  letter-spacing: 2px;
  line-height: 15px;
  font-size: 20px;
  color: #ffffff;
  background-color: #014965;
  text-align: center;
  text-transform: capitalize;
}
#lacer-menu .item{
  width: 100%;
  height: auto;
  padding-top: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
  font-size: 20px;
  color: #014965;
  text-align: center;
  text-transform: capitalize;
}
#lacer-menu .item a{
  text-decoration: none;
  color: #014965;
}
#lacer-menu .item:hover{
  background-color: #129FEA;
  cursor: pointer;
}
#lacer-view{
  height: 450px;
  margin-top: 50px;
  background-color: #fff;
  text-align: center;
  -webkit-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  -moz-box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
  box-shadow: 1px 1px 9px 2px rgba(204,204,204,1);
}

#lacer-view .head {
  width: 100%;
  padding-top: 15px;
  background-color: #fff;
  border-bottom: 2px solid #f5f5f5;


}

#lacer-view .head i {
  margin-top: 10px;
  color: lightblue;
  font-size: 16px;
  margin-right: 5px;
}

#lacer-view .head span {
  margin-right: 15px;
}
.tase {
  font-family: 'Raleway', sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  margin: 0 0 24px;
  text-align: center;
  text-transform: uppercase;
  color: #014965;
}

.tasemini {
  font-size: 12px;
  letter-spacing:0.5px;
  color: #DFBF84;
  text-transform: uppercase;
//  text-shadow: 1px 1px 0 #000, margin : 10 px 0 24 px;
  text-align: center;
  line-height: 30px;
}



.current-view{
  width: 100%;
  height: auto;
  padding-top: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #999999;
  letter-spacing: 2px;
  line-height: 15px;
  font-size: 20px;
  color: #ffffff;
  background-color: #014965;
  text-align: center;
  text-transform: capitalize;
}

.control-btns{
  margin-bottom:10px;padding-right:10px;border-top: 0px solid #ccc
}

.control-btns button{
  margin: 20px;
}
